<template>
  <AppLayout>
    <div>PostTransform</div>
    <section>
      <layer class="no-pointer-events">
        <VERect size="150" label="transform" :style="boxTransform01"></VERect>
      </layer>
      <layer class="no-pointer-events">
        <VERect size="150" label="" theme="frame"></VERect>
        <VEPoint :x="box01.perspectiveOrigin.x" :y="box01.perspectiveOrigin.y" label="perspective-origin" style="opacity:.4"></VEPoint>
      </layer>
      <div style="min-height:170px;"></div>
      <div class="no-word-break"></div>
      <table style="width:600px;">
        <colgroup>
          <col style="width:120px">
          <col>
          <col style="width:55px">
        </colgroup>
        <tbody>
          <tr>
            <th>rotateX</th>
            <td><VESlider v-model="box01.rotateX" input-cycle="enter" min-value="-360" max-value="360"></VESlider></td>
            <td>{{box01.rotateX}}</td>
          </tr>
          <tr>
            <th>rotateY</th>
            <td><VESlider v-model="box01.rotateY" input-cycle="enter" min-value="-360" max-value="360"></VESlider></td>
            <td>{{box01.rotateY}}</td>
          </tr>
          <tr>
            <th>perspective</th>
            <td><VESlider v-model="box01.perspective" input-cycle="enter" min-value="100" max-value="500"></VESlider></td>
            <td>{{box01.perspective}}</td>
          </tr>
          <tr>
            <th colspan="3">
              <VEPointSlider 
                v-model="box01.perspectiveOrigin"
                input-cycle="enter"
                x-max-value="150"
                y-max-value="150"
              ></VEPointSlider>
            </th>
          </tr>
        </tbody>
      </table>
    </section>
    
  </AppLayout>
</template>
<script>
import AppLayout from "../layout/AppLayout.vue";
import { Layer, VERect, VESlider, VEPoint, VEPointSlider } from '../component';
import { transformVariant } from '../../../../.src/web';

export default {
  components:{
    AppLayout,
    Layer,
    VEPoint,
    VERect,
    VESlider,
    VEPointSlider
  },
  computed:{
    boxTransform01 (){
      return { 
        transform: transformVariant(this.box01), 
        opacity:.7
      };
    }
  },
  data (){
    return {
      box01:{ rotateX:0, rotateY:0, perspective:300, perspectiveOrigin:{x:75,y:75} }
    }
  }
}
</script>